@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");

:root {
  --pageBackground: #f9f9f9;
  --defaultFont: -apple-system, BlinkMacSystemFont, "Inter", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  --blockBoxShadow: 0px 4px 15px rgb(231, 231, 231);
  --mobileWidth: 1000px;
  --accentColor: #4978ff;
  --primaryText: rgb(31, 35, 40);
  --containerBackground: rgba(249, 249, 249, 0.8);
  --developersWrapperWidth: 1400px;
}

@media (max-width: 1000px) {
  :root {
    --containerBackground: rgba(249, 249, 249, 1);
  }
}

*,
button,
input,
textarea {
  font-family: "Inter", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  outline: none;
}

*::selection {
  background-color: rgba(0, 0, 0, 0.1);
}

body {
  margin: 0;
  padding: 0;
}

p.required::after {
  content: " *";
  color: red;
}

select,
button {
  color: #000;
}

select,
input {
  background: white;
}

@media (min-width: 1000px) {
  #R-A-2536124-2 {
    display: none;
  }
}
